.item
{
    width:350px;
}
.item p
{
    margin:6px 0px;
}
.item-prices
{
    display: flex;
    gap:20px;
}
.item-price-new
{
    color:black;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old
{
    color:black;
    font-size: 18px;
    font-weight: 500px;
    text-decoration: line-through;
}
.item:hover
{
    transform: scale(1.05);
    transition: 0.6s;
}
